.profile__name {
  color: #fff;
  margin: 0;
  font-family: 'Inter', Arial, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 400px;
}

@media (max-width: 680px) {
  .profile__name {
    font-size: 27px;
    line-height: 33px;
    text-align: center;
  }
}